	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 13 - Fuite pour la liberté","texte":{"childMarkdownRemark":{"html":"<p>C’est le soir, je suis resté éveillé pour profiter du fait que tout le monde dormait pour enfin partir d’ici. J’avais réussi à les mettres en confiance suffisamment pour qu’ils ne se soucient plus de moi. Je suis assez en forme pour utiliser mon pouvoir sur la gravité si nécessaire. Vers 23h, je me lève et verrouille la porte de ma chambre, je vais à côté de la fenêtre. Je suis au troisième étage, mais ça ne devrait pas poser problème grâce à mon pouvoir. J’ouvre la fenêtre, je prend mon sac et le fait descendre via une corde que j’ai attachée sur les ganses de mon sac. Je fais le moins de bruit possible, si je me fais voir, je suis foutu. Je prend une grande inspiration et saute. J’utilise mon pouvoir pour me réceptionner sans bruit. Je prend mon sac en vérifiant que personne n’était dans les environs. Je me relève et part vers la frontière qui mène à Mahinu. J’ai bien peur d’avoir à voler une voiture ou un camion. Non, ça ferait trop de bruit dans les environs. Je dois m’éloigner suffisamment avant de faire ça.</p>\n<p>Après 30 minutes de marche dans la ville, je vois une voiture abandonnée, mais qui semble en état d’être conduite. Je m’approche du côté conducteur et il n’y avait pas de fenêtre. Quelqu’un l’avait sûrement volée et laissée là. Je regarde autour de moi, vérifiant qu’il n’y avait personne et j’ouvre la portière et ouvre sous le volant pour mettre le contact. Je pars enfin vers la frontière, j’espère en un sens que la police ne se mettra pas sur mon chemin, je me vois mal expliquer tout ce qui ce passe.</p>\n<p>Du côté de la Guilde, Éléa arrivait, elle venait porter les uniformes de la Guilde, Wamien avait invité tout le monde à venir dans la salle commune pour recevoir les tenues. Clara souriait en voyant comment Wamien était exité par cette nouveautée. Elle vint se blottir contre lui et mit une main sur son épaule.</p>\n<p>Clara: Du calme chou, tu vas faire peur à tout le monde.<br>\nWamien: Ça va être officiel, on va avoir des uniformes, les gens vont pouvoir savoir qui nous sommes quand on va intervenir.<br>\nClara: Je sais chou, je sais, mais en attendant, tu dois essayer de te calmer, parce qu’on dirait un enfant qui attend des cadeaux.<br>\nWamien: Bon, d’accord, je me calme.</p>\n<p>Wamien sourit et va s’asseoir. Clara va rejoindre Thalie et Louann qui parlaient dehors. Sam était assis sur le toit et regardait vers où Éléa devait arriver. Wamien n’était pas le seul à avoir hâte. Sam regarde l’horizon et voit les lumières de la voiture d’Éléa au loin.</p>\n<p>Sam: Elle arrive! Les uniformes arrivent!</p>\n<p>Sam sort ses ailes, saute du toit de la maison et range ses ailes après s’être posé devant les filles qui le regardaient avec des gros yeux.</p>\n<p>Louann: Voyons Sam! Du calme, fait nous pas peur comme ça, j’ai pratiquement renversé mon chocolat chaud.<br>\nSam: Pardon Louann, mais les uniformes arrivent! J’ai vu les phares de la voiture d’Éléa au loin.<br>\nClara: T’es pire que Wamien toi!</p>\n<p>Clara rigole doucement et Thalie sourit en regardant la tête que Sam faisait.</p>\n<p>Sam: Sérieux les filles, vous n’avez pas plus hâte que ça?<br>\nThalie: Si, mais on sait rester calme.</p>\n<p>Les lumières s’approchent et Éléa stationne sa voiture. On la voit bailler au travers le parebrise, la route a dû être longue, surtout seule. Sam s’approche de la voiture. Kameron, Tim et Hiro sortent pour aider à sortir les boîtes qui contiennent les uniformes.</p>\n<p>Sam: Aller les filles, on vient aider, on va rentrer les boîtes.</p>\n<p>Éléa sort de la voiture et s’étire. Les filles viennent aider et on fait la file en se passant les boîtes pour que ça ailles plus rapidement, Wamien sort et va voir Éléa avec un chocolat chaud.</p>\n<p>Wamien: Salut Éléa, merci d’avoir transporté ça ici.<br>\nÉléa: De rien, Wamien.<br>\nWamien: Tiens, un chocolat chaud.<br>\nÉléa: Essaierais-tu de m’acheter?</p>\n<p>Éléa le regarda en souriant.</p>\n<p>Wamien: Hein? Non, c’est pour te remercier et si tu veux, on a une chambre de libre pour toi, question que tu ne doives pas faire la route de nuit.<br>\nÉléa: Bon d’accord.</p>\n<p>Éléa sourit en prenant la tasse et but une gorgée. Elle sourit en regardant tout le monde travailler main dans la main pour faire rentrer les uniformes.</p>\n<p>Éléa: D’accord, je vais rester pour la nuit, mais c’est bien parce que tu insistes.<br>\nWamien: Pas de soucis.</p>\n<p>Wamien sourit et alla aider en prenant une boîte et en la transportant à l’intérieur. Éléa entra aussi question de voir les changements que La Guilde avait fait dans le coin. L’endroit était chaleureux et remplis de vie même si les personnes qui vivaient ici avaient pour la plupart déménagé dans les maisons sur le domaine de Clara. Éléa remarqua le sac à dos de son fils qui trainait dans le salon. Elle alla voir Wamien avec le sac en question.</p>\n<p>Éléa: Wamien? Tu sais à qui appartient ce sac?</p>\n<p>Elle le lui montra. Wamien la regarda un peu songeur.</p>\n<p>Wamien: C’est celui d’un nouveau, William, il est arrivé il y a un peu plus d’un mois.<br>\nÉléa: William?! Mon William? 15 ans, grand comme ça? Il sait contrôler l’humeur des autres?</p>\n<p>Je la regarde, je réalise qu’il nous avait tous mentis.</p>\n<p>Wamien: C’est exactement ça.<br>\nÉléa: Et tu ne t’ais pas demandé d’où ce jeune sortait?<br>\nWamien: Il m’a dit que ses parents étaient décédés depuis plus de 4 ans.<br>\nÉléa: Oh le petit maudit!<br>\nWamien: Il vit avec Hiro, c’est son apprentis.<br>\nÉléa: Excuse-moi Wamien, mais je dois aller le voir. Il a fugué parce que l’on s’était disputé, son père et lui parce qu’il avait utilisé ses pouvoirs sur ses petites soeurs.<br>\nWamien: Je ne savais pas que tu avais un enfant?<br>\nÉléa: J’en ai trois.</p>\n<p>Elle fit demi-tour et alla vers la maison d’Hiro, elle savait où c’était puisque le nom d’Hiro était écrit sur la deventure de la maison. Éléa s’approcha de la porte et cogna.</p>\n<p>Éléa: William! Sort! Je sais que tu es là!</p>\n<p>Wamien vient la rejoindre, sort ses clés pendant qu’Hiro arrive.</p>\n<p>Hiro: Il se passe quoi?<br>\nWamien: William est le fils d’Éléa, il nous a tous mentis.<br>\nHiro: Oh. Passer par l’arrière, il est en entraînement à cette heure.</p>\n<p>Éléa descendit du balcon et fit le tour de la maison. Elle figea lorsqu’elle vit son fils. Il était assit dans l’herbe et méditait, il n’avait rien entendu des appels de sa mère puisqu’il avait ses écouteurs. Éléa s’approcha d’un pas décidé, prête à remettre son fils à l’ordre. Wamien et Hiro regardaient de loin, ça allait barder pour l'adolescent.</p>\n<p>Éléa: William Drumel! Cria Éléa, une fois rendue à côté de son fils.</p>\n<p>William fit un bond et se tourna vers la voix qu’il avait entendu. Il figea en voyant le regard de sa mère. Il avait trop peur pour dire quoi que ce soit ni même pour utiliser son pouvoir.</p>\n<p>Éléa: Tu ne sais pas à quel point on était inquiet pour toi! Fuguer comme ça à ton âge!<br>\nWilliam: Ma… maman, je.<br>\nÉléa: Debout et va ranger tes affaires, on rentre à la maison!<br>\nWilliam: Mais, je, je suis bien ici…<br>\nÉléa: Tu reviendras quand tu auras l’âge, en attendant, tu reviens à la maison, pas de chichi!<br>\nWamien: William tu devrais écouter ta mère.<br>\nÉléa: Toi Wamien ne t’en mèle pas!</p>\n<p>Wamien recule un peu pour signifier son retrait au conflit et regarda Hiro.</p>\n<p>Wamien: Va aider à rentrer les autres boîtes.</p>\n<p>William se levait et rentra pour aller ramasser ses choses. Wamien alla vérifier que tout avait été rentrés. Éléa faisait les cents pas devant sa voiture, essayant de se calmer. William fini par sortir avec son sac et Éléa lui ouvre la porte.</p>\n<p>Éléa: Monte.</p>\n<p>William montre dans la voiture et s’attache. Wamien et Éléa se regardent.</p>\n<p>Éléa: Merci d’avoir prit soin de lui au moins il était en sécurité.<br>\nWamien: De rien Éléa, encore pardon pour tout, j’aurais dû enquêter davantage.</p>\n<p>Éléa hocha un peu la tête et va monter dans la voiture.</p>\n<p>Éléa: Bonne soirée.<br>\nWamien: Merci vous aussi et bonne route.</p>\n<p>Éléa part et William nous regardait en s’éloignant. Angélique avait regardé la scène depuis sa chambre et avait peur que sa mère arrive aussi. Elle décide d’aller prendre téléphone et appela sa mère. Il n’y avait pas de réponses, d’elle laisse donc un message.</p>\n<p>Angélique: Salut maman, c’est Angélique, juste pour te dire que je vais bien, je suis avec La Guilde et j’apprend à mieux contrôler mes pouvoirs. Je suis nourrie et logée. Ne t’en fait pas pour moi. Je t’aime, maman.</p>\n<p>Elle raccroche, les larmes aux yeux. Elle se demande pourquoi elle ne l’a pas contactée plus tôt. Elle devait être morte d’inquiétude.</p>\n<p>Du côté de Daryl, il arrive enfin à la frontière et s’approche du bâtiment où le douanier travaillait. Il se stationna et sorti du véhicule pour aller voir le douanier. Pas question qu’il se fasse prendre avec un véhicule volé.</p>\n<p>Daryl: Bonsoir monsieur.<br>\nDouanier: Bonsoir, papier et motif de passage s’il vous plaît.<br>\nDaryl: Je n’ai pas mes papiers, je me nomme Daryl Koido. Vous pouvez joindre Darakei Drumel pour valider.<br>\nDouanier: Je ne fais pas d’appel à cette heure et encore moins question de chercher le numéro.<br>\nDaryl: Je connais le numéro.</p>\n<p>Le douanier soupire, il n’aimait clairement pas son travail. Il lui tendit un papier et un crayon pour qu’il écrive le numéro. Daryl écrivit le numéro qui était le numéro direct pour rejoindre Darakei. Le douanier compose le numéro.</p>\n<p>Douanier: Bonsoir monsieur Drumel, je présume. Je suis le douanier du poste 4 entre Giotry et Deracus et j’ai un homme ici qui dit vous connaître. Son nom est Daryl Kiodo.<br>\nDaryl: Koidi, monsieur.<br>\nDouanier: Daryl Koido, pardon.</p>\n<p>Le douanier recule le téléphone de son oreille puisque Darakei criait, Daryl pouvait très bien entendre.</p>\n<p>Darakei: Passez-le moi immédiatement! Je dois lui parler!</p>\n<p>Le douanier tend le téléphone vers Daryl. Daryl le prend et le pose sur son oreille.</p>\n<p>Daryl: Salut Darakei.<br>\nDarakei: Daryl! Comment ça va?<br>\nDaryl: Ça va, je me suis échappé d’où j’étais retenu.</p>\n<p>Le douanier le dévisagea, il trouvait ça suspect qu’il dise qu’il s’était échappé d’un certain endroit. Daryl se mit dos au douanier.</p>\n<p>Daryl: Tu peux envoyer quelqu’un pour venir me chercher, je t’expliquerais tout une fois revenu.<br>\nDarakei: J’envoie Éléa venir te chercher.<br>\nDaryl: Parfait, merci Darakei.</p>\n<p>Daryl raccroche et se retourne vers le douanier. Le douanier prend le téléphone.</p>\n<p>Douanier: Je me dois de poser la question, mais es-tu un évadé de prison?<br>\nDaryl: Pas du tout, j’étais retenu chez un groupe de criminel et j’ai réussi à m’enfuir.<br>\nDouanier: Un groupe de criminel?<br>\nDaryl: Oui, le genre qui utilisent leurs pouvoirs pour faire le mal.<br>\nDouanier: Je ne veux pas de problèmes avec les nouveaux humains. Je ne veux pas en savoir plus.<br>\nDaryl: Voilà, merci et bonne soirée.</p>\n<p>Daryl va s’asseoir sur un des bancs en attendant son transport.</p>"}},"chapitreAvant":"chapitre-12-vol","chapitreApres":"chapitre-14-l-inhibiteur","nomRoman":"../le-premier-cyborg-tome-2","slug":"chapitre-13-fuite-pour-la-liberte"}},"pathContext":{"slug":"chapitre-13-fuite-pour-la-liberte"}}

